.RegionsList .Cell .Cell__children {
  display: flex;
}

.RegionsList .Cell .Div {
  flex: 1;
  padding: 6px;
}

/* Прячем иконки в шапке */
.RegionsList .Cell:nth-child(1) .Icon {
  opacity: 0;
}

/* Жирный заголовок столбцов */
.RegionsList .Cell:nth-child(1) {
  font-weight: bold;
}

/* Первая колонка с названием региона самая широкая */
.RegionsList .Cell .Div:nth-child(1) {
  flex: 3;
  
  /* Многоточие если текст не влазит */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.RegionsList .Cell .Div .Counter {
  display: inline-block;
  margin-left: 8px;
}
